import {Link, useParams} from 'react-router-dom';
import {useState, useEffect} from 'react';
import { Helmet } from 'react-helmet';

import MarvelService from '../../services/MarvelService';
import Spinner from '../spinner/Spinner';
import Error404 from './Error404';

import './singleComicPage.scss';

const SingleComicPage = () => {

    const {comicId} = useParams();
    const [comic, setComic] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);

    const marvelService = new MarvelService();

    useEffect(() => {
        updateComic();
    }, [comicId]);

    const updateComic = () => {
        onComicLoading();
        marvelService
            .getComic(comicId)
            .then(onComicLoaded)
            .catch(onError);
    }

    const onComicLoaded = (comic) => {
        setComic(comic);
        setLoading(false);
    }

    const onComicLoading = () => {
        setLoading(true);
    }

    const onError = () => {
        setError(true);
        setLoading(false);
    }

    const errorMessage = error ? <Error404/> : null;
    const spinner = loading ? <Spinner/> : null;
    const content = !(loading || error || !comic) ? <View comic={comic}/> : null;

    return (
        <>
            {errorMessage}
            {spinner}
            {content}
        </>
    )
}

const View = ({comic}) => {
    const {title, description, thumbnail, pageCount, price, language, urls} = comic;

    return (
        <div className="single-comic">
            <Helmet>
                <meta
                    name="description"
                    content={`${title} comics book`}
                />
                <title>{title}</title>
            </Helmet>
            <img src={thumbnail} alt={title} className="single-comic__img"/>
            <div className="single-comic__info">
                <h2 className="single-comic__name">{title}</h2>
                <p className="single-comic__descr">{description}</p>
                <p className="single-comic__descr">{pageCount}</p>
                <p className="single-comic__descr">Language: {language}</p>
                <a href={urls} target='blank' alt={price} className="single-comic__price">{price ? price : 'not available'}</a>
            </div>
            <Link to={'/comics'} className="single-comic__back">Back to all</Link>
        </div>
    ) 
}

export default SingleComicPage;